import React, { useState } from "react";
import styleVars from "../../styles/style-vars";
import Icon from "../Icon";
import HeaderMenu from "./HeaderMenu";



const Header = ({ className }) => {
  const [menu, setMenu] = useState(false);
  const isCommunity =
    typeof window !== "undefined" ? window.location.pathname === "/" : false;
  const isAthletes =
    typeof window !== "undefined"
      ? window.location.pathname?.includes?.("athletes")
      : false;
  const isAbout = 
    typeof window !== "undefined"
      ? window.location.pathname?.includes?.("about")
      : false;
  return (
    <div className={`${className} header-container`} id="header-container">
      <div
        className={`${styleVars.page.xl} grid-12 gutter-row-20 gutter-36-ns pt6 pb6 align-center`}
      >
        <HeaderMenu
          menu={menu}
          setMenu={setMenu}
        />
        <div className="col-6 col-2-l flex">
          <a href="/">
            <Icon name="logo.png" className="ma0 pa0 mh4" />
          </a>
        </div>
        <div className="col-4-l dn flex-l justify-center">
        </div>
        <div className="col-6-l dn flex-l ml-auto align-center">
          <a
            className={`primary-text-color ma0 pa0 f5 mr6 ${
              isCommunity ? "fw-bold" : ""
            }`}
            href="/"
          >
            Community
          </a>
          <a
            className={`primary-text-color ma0 pa0 f5 mr6 ${
              isAthletes ? "fw-bold" : ""
            }`}
            href="/athletes"
          >
            Athletes
          </a>
          <a
            className={`primary-text-color ma0 pa0 f5 mr6 ${
              isAbout ? "fw-bold" : ""
            }`}
            href="/about"
          >
            About
          </a>
          {/*
          <a
            className="btn-base btn-outlined pa2 f6 tc"
            style={{ minWidth: 150 }}
            target="_blank"
            href={
              isAthletes
                ? ""
                : ""
            }
          >
            {isAthletes ? "Get Started" : "Get Started"}
          </a>
          */}
        </div>
        <div className="col-6 flex justify-end dn-l">
          <Icon
            name="menu"
            className="ma0 pa0 mh2 ml4 db dn-l"
            onClick={() => setMenu(!menu)}
          />
        </div>
      </div>
    </div>
  );
};

export default Header;
